<template>
    <div class="login">
        <el-card class="box-card">
            <div slot="header" class="clearfix">
                <span>Anakin商城欢迎光顾</span>
            </div>
            <el-tabs v-model="currentIndex" stretch @tab-click ="handleTabsClick" >
                <el-tab-pane label="登陆" name="login">
                    <el-form :model="loginForm" :rules="rules" status-icon ref="loginForm">
                        <el-form-item label="用户名" label-width="80px" prop="username">
                            <el-input type="text" v-model="registerForm.username"  />  
                        </el-form-item>
                        <el-form-item label="密码" label-width="80px" prop="password">
                            <el-input type="password" v-model="loginForm.password" />
                        </el-form-item>
                        <el-from-item>
                            <el-button type="primary" @click="submitForm('loginForm')">提交</el-button>
                        </el-from-item>
                    </el-form>
                </el-tab-pane>
                <el-tab-pane label="注册" name="register">
                    <el-form :model="registerForm" :rules="rules" status-icon ref="registerForm">
                        <el-form-item label="用户名" label-width="80px" prop="username">
                            <el-input type="text" v-model="registerForm.username"  />  
                        </el-form-item>

                        <el-form-item label="密码" label-width="80px" prop="password">
                            <el-input type="password" v-model="registerForm.password" />
                        </el-form-item>

                        <el-form-item label="确认密码" label-width="80px" prop="configurePassword">
                            <el-input type="password" v-model="registerForm.configurePassword" />
                        </el-form-item>

                        <el-from-item>
                            <el-button type="primary" @click="submitForm('registerForm')">提交</el-button>
                        </el-from-item>
                    </el-form>
                </el-tab-pane>
            </el-tabs>
        </el-card>
    </div>
     
</template>

<script>
  
  
  export default {
        data() {
            //验证规则
            var validateUserName = (rule, value, callback) =>{
                if(value === ''){
                    callback(new Error("请输入用户名"))
                }else if(value.length > 6){
                    callback(new Error("长度不够"))
                }    
                else{
                    callback();
                }
            }

            var validatePassWord = (rule, value, callback) =>{
                if(value === ''){
                    callback(new Error("请输入密码"))
                }else{
                    callback();
                }
            }

            var validateConfigurePassword = (rule, value, callback) =>{
                if(value === ""){
                    callback(new Error("请输入密码"));
                }else if(value !== this.registerForm.password){
                    callback(new Error("输入密码不一致"));
                }else{
                    callback();
                }
            }
            return{
                currentIndex:'login',
                loginForm:{
                    username:'',
                    password:''
                    
                },
                registerForm:{
                    username:'',
                    password:'',
                    configurePassword:''
                },
                activeTab:"login",
                rules:{
                    username:[
                        {
                           validator:validateUserName,
                           trigger:"blur" 
                        }
                    ],
                    password:[
                        {
                            validator:validatePassWord,
                            trigger:'blur'
                        }
                    ],
                    configurePassword:[
                        {
                            validator:validateConfigurePassword,
                            trigger:'blur',
                        }
                    ]
                }
            } 
        },
        
        methods:{
            submitForm(formName){
                this.$refs[formName].validate((valid) =>{
                    if(valid){
                        if(this.activeTab === "login"){
                            //登陆
                        }
                        if(this.activeTab === "register"){
                            //注册
                        }
                        console.log(this.loginForm)
                    }else{
                        return;
                    }
                });
            },
            handleTabsClick(tab){
                console.log(tab.name);
                this.activeTab = tab.name;
            }  
        }
  }
</script>

<style scoped lang="less">

*{
    text-align: center;
}
.login{
    width:1200px;
    margin:0 auto;
    .box-card{
        width:500px;
        margin:100px auto;
    }
}
</style>